import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import MyProjects from '@/views/MyProjects'
import MyEbooks from '@/views/MyEbooks'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/myprojects',
      name: 'MyProjects',
      component: MyProjects
    },
    {
      path: '/myebooks',
      name: 'MyEbooks',
      component: MyEbooks
    }
  ]
})



